import "bootstrap/dist/css/bootstrap.min.css";
import "./App.css";
import { useState, useEffect } from "react";
import { useInterval } from "usehooks-ts";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  TimeScale,
  TimeSeriesScale,
  BarElement,
  Filler,
} from "chart.js";
import { Line, Chart } from "react-chartjs-2";
import { DateTime } from "luxon";
import "chartjs-adapter-luxon";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
const axios = require("axios").default;

ChartJS.register(
  Filler,
  CategoryScale,
  LinearScale,
  TimeScale,
  PointElement,
  LineElement,
  TimeSeriesScale,
  Title,
  Tooltip,
  Legend,
  BarElement
);

function App() {
  let [data, setData] = useState([{ value: "" }]);
  let [boolData, setBoolData] = useState([{ value: "" }]);
  let [labels, setLabels] = useState([]);
  let [chartData, setChartData] = useState([]);
  let [boolChartData, setBoolChartData] = useState([]);
  let [startDate, setStartDate] = useState(new Date());
  let [endDate, setEndDate] = useState(new Date());
  let [live, setLive] = useState(1);
  let [active, setActive] = useState(0);
  useEffect(() => {
    updateData();
  }, []);

  const updateData = () => {
    axios
      .get("https://boopbeep.herokuapp.com/api")
      .then((response) => {
        setData(response.data.filter((el) => el.variable === "Pressure"));
        setBoolData(response.data.filter((el) => el.variable === "Boolean"));
      })
      .catch((error) => {
        console.log(error);
      });
  };

  const eraseData = () => {
    axios
      .get("https://boopbeep.herokuapp.com/api/drop")
      .then((response) => {
        console.log(response);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  useEffect(() => {
    if (live == 1) {
      if (data[0] && active == 0) {
        if (data[0].date) {
          setStartDate(new Date(Number(data[0].date)));
          setEndDate(new Date(Number(boolData[boolData.length - 1].date)));
          setActive(1);
        }
      } else if (data[0]) {
        if (data[0].date) {
          //setStartDate(new Date(Number(data[0].date)));
          setEndDate(new Date(Number(boolData[boolData.length - 1].date)));
          setActive(1);
        }
      }
    }
  }, [data]);

  useInterval(() => {
    axios
      .get("https://boopbeep.herokuapp.com/api")
      .then((response) => {
        if (
          response.data.filter((el) => el.variable == "Pressure").length !=
          data.length
        ) {
          updateData();
        } else if (
          response.data.filter((el) => el.variable == "Boolean").length !=
          boolData.length
        ) {
          updateData();
        }
      })
      .catch((error) => {
        console.log(error);
      });
  }, 5000);

  useEffect(() => {
    let new_chart_data = data
      .map((el) => {
        if (
          DateTime.fromMillis(Number(el.date)) >=
            DateTime.fromJSDate(startDate) &&
          DateTime.fromMillis(Number(el.date)) <= DateTime.fromJSDate(endDate)
        ) {
          return { y: el.value, x: DateTime.fromMillis(Number(el.date)) };
        }
      })
      .filter((el) => el);
    let new_bool_chart_data = boolData
      .map((el) => {
        if (
          DateTime.fromMillis(Number(el.date)) >=
            DateTime.fromJSDate(startDate) &&
          DateTime.fromMillis(Number(el.date)) <= DateTime.fromJSDate(endDate)
        ) {
          return { y: el.value, x: DateTime.fromMillis(Number(el.date)) };
        }
      })
      .filter((el) => el);
    setChartData(new_chart_data);
    setBoolChartData(new_bool_chart_data);
  }, [data, boolData, startDate, endDate]);

  const changeStartDate = (newDate) => {
    //console.log(newDate)
    //console.log(DateTime.fromJSDate(newDate).toMillis());
    setStartDate(newDate);
  };

  const changeEndDate = (newDate) => {
    //console.log(newDate)
    //console.log(DateTime.fromJSDate(newDate).toMillis());
    setEndDate(newDate);
  };

  const toggleLive = () => {
    setLive(live == 1 ? 0 : 1);
  };

  if (data.length > 0) {
    if (chartData.length > 0) {
      //console.log(chartData)
      return (
        <div className="container-fluid">
          <div class="title h3 text-center py-2">Dashboard</div>
          <div className="py-2 mx-2">
            <div className="device-title">Temperature Controller </div>
            <h6>Latest Readings</h6>
            <table>
              <thead>
                <td>Variable</td>
                <td>Reading</td>
                <td>Time</td>
              </thead>
              <tbody>
                <tr>
                  <td>Temperature</td>
                  <td>{data[data.length - 1].value} C</td>
                  <td>
                    {DateTime.fromMillis(
                      Number(data[data.length - 1].date)
                    ).toFormat("dd/MM/y HH:mm:ss a")}
                  </td>
                </tr>
                <tr>
                  <td>Relay Status</td>
                  <td>
                    {boolData[boolData.length - 1].value == 1 ? "ON" : "OFF"}
                  </td>
                  <td>
                    {DateTime.fromMillis(
                      Number(boolData[boolData.length - 1].date)
                    ).toFormat("dd/MM/y HH:mm:ss a")}
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <div className="py-2 mx-2">
            <h6>Graph </h6>
            <div>
              Start Time:{" "}
              <DatePicker
                selected={startDate}
                onChange={changeStartDate}
                showTimeSelect
                dateFormat="MMM d, yyyy h:mm:ss aa"
              />
              End Time:{" "}
              <DatePicker
                selected={endDate}
                onChange={changeEndDate}
                showTimeSelect
                dateFormat="MMM d, yyyy h:mm:ss aa"
              />
            </div>
            <div style={{'minHeight': '270px'}}>
            <Line
              data={{
                datasets: [
                  {
                    type: "line",
                    data: chartData,
                    label: "Temperature(C)",
                    borderColor: "#3333ff",
                    borderWidth: 2,
                    yAxisID: "y",
                  },
                  {
                    type: "line",
                    data: boolChartData,
                    label: "Relay Status",
                    backgroundColor: "#00ff00",
                    borderColor: "#1a962e",
                    yAxisID: "y1",
                    stepped: true,
                    fill: true,
                  },
                ],
              }}
              options={{
                maintainAspectRatio:false,
                scales: {
                  y: {
                    type: "linear",
                    display: true,
                    position: "left",
                    title: {
                      display: true,
                      text: "Temperature(C)",
                    },
                  },
                  y1: {
                    type: "linear",
                    display: true,
                    position: "right",
                    title: {
                      display: true,
                      text: "Relay Status",
                    },
                  },
                  x: {
                    type: "time",
                    time: {
                      displayFormats: {
                        millisecond: "d/M/y HH:mm:ss",
                        second: "d/M/y HH:mm:ss",
                        minute: "d/M/y HH:mm:ss",
                        hour: "d/M/y HH:mm:ss",
                        day: "d/M/y HH:mm:ss",
                        week: "d/M/y HH:mm:ss",
                        month: "d/M/y HH:mm:ss",
                        quarter: "d/M/y HH:mm:ss",
                        year: "d/M/y HH:mm:ss",
                      },
                    },
                    title: {
                      display: true,
                      text: "Time",
                    },
                    ticks: {
                      autoSkip: true,
                    },
                  },
                },
                legend: {
                  display: true,
                },
                elements: {
                  line: {
                    borderWidth: 1,
                  },
                },
              }}
            />
            </div>
          </div>
          <div className=" text-center">
            <button
              type="button"
              onClick={toggleLive}
              className={
                live == 1
                  ? "btn-live btn btn-success"
                  : "btn-live btn btn-success btn-live-disabled"
              }
            >
              Live
            </button>

            <button
              type="button"
              onClick={eraseData}
              className="btn btn-danger"
            >
              Erase Data
            </button>
          </div>
        </div>
      );
    } else {
      return (
        <div className="container-fluid">
          <div class="title h3 text-center py-2">Dashboard</div>
          <div className="py-2 mx-2">
            <div className="device-title">Temperature Controller </div>
            <h6>Latest Readings</h6>
            <table>
              <thead>
                <td>Variable</td>
                <td>Reading</td>
                <td>Time</td>
              </thead>
              <tbody>
                <tr>
                  <td>Temperature</td>
                  <td>{data[data.length - 1].value} C</td>
                  <td>
                    {DateTime.fromMillis(
                      Number(data[data.length - 1].date)
                    ).toFormat("dd/MM/y HH:mm:ss a")}
                  </td>
                </tr>
                <tr>
                  <td>Relay Status</td>
                  <td>
                    {boolData[boolData.length - 1].value == 1 ? "ON" : "OFF"}
                  </td>
                  <td>
                    {DateTime.fromMillis(
                      Number(boolData[boolData.length - 1].date)
                    ).toFormat("dd/MM/y HH:mm:ss a")}
                  </td>
                </tr>
              </tbody>
            </table>
          </div>
          <div className=" col-md-6 py-2 mx-2">
            <h6>Graph </h6>
            <div>
              Start Time:{" "}
              <DatePicker
                selected={startDate}
                onChange={changeStartDate}
                showTimeSelect
                dateFormat="MMM d, yyyy h:mm aa"
              />
              End Time:{" "}
              <DatePicker
                selected={endDate}
                onChange={changeEndDate}
                showTimeSelect
                dateFormat="MMM d, yyyy h:mm aa"
              />
            </div>
          </div>
          <button
            type="button"
            onClick={toggleLive}
            className={
              live == 1
                ? "btn-live btn btn-success"
                : "btn-live btn btn-success btn-live-disabled"
            }
          >
            Live
          </button>
          <button onClick={eraseData} className="primary-btn">
            Erase Data
          </button>
        </div>
      );
    }
  } else {
    return (
      <div className="container-fluid">
        <div class="title h3 text-center py-2">Dashboard</div>
        <div className="py-2 mx-2">
          <div className="device-title">Temperature Controller </div>
          <h3>No Data to Display!</h3>
        </div>
      </div>
    );
  }
}

export default App;
